.header {
  width: 100%;
  display: block;
  min-height: 144px;
  padding-top: 0px;
}

.header-title {
  font-size: 20px;
  font-weight: 700;
  color: #0082c8;
}
.wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0px;
  min-width: 320px;
}

.logo {
  padding: 20px 0px 10px 0px;
  margin: 0 auto;
}
.searchdestop {
  margin-top: 10px;
  padding-right: 10px;
}
.searchdestop {
  display: block;
  position: absolute;
  z-index: 100;
  height: 52px;
  top: 0px;
  right: 0px;
}
.icon {
  float: left;
  color: #fd6504;
  cursor: pointer;
}
input[type="text"] {
  padding: 2px 2px;
  outline: none;
  display: block;
  background: #fff;
  text-indent: 4px;
  color: #2c2c2c;
  border: solid 1px #b9b9b9;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
}
.nut-tim {
  font-size: 20px;
  color: #0082c8;
  cursor: pointer;
}
.container-fluid {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.wsmenu {
  font-family: Helvetica, sans-serif;
  color: #fff;
  position: relative;
  font-size: 15px;
  padding: 0px;
  margin: 0px auto;
  width: 100%;
  max-width: 1200px;
  background-color: #0082c8;
}
.wsmenu-list {
  text-align: left;
  margin: 0 auto 0 auto;
  width: 100%;
  padding: 0px;
}
.wsmenu-list > li {
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}
.wsmenu-list > li > a.active {
  background-color: rgba(0, 0, 0, 0.08) !important;
  text-decoration: none;
}
.wsmenu-list > li > a .fa {
  display: inline-block;
  font-size: 20px;
  line-height: inherit;
  margin-right: 0px;
}
.hometext {
  display: none;
}
.wsmenu-list li > .megamenu {
  transform-style: preserve-3d;
  -o-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transform: rotateX(-75deg);
  -o-transform: rotateX(-75deg);
  -moz-transform: rotateX(-75deg);
  -webkit-transform: rotateX(-75deg);
  visibility: hidden;
}
.halfmenu {
  width: 60%;
  right: auto !important;
  left: auto !important;
}
.megamenu {
  width: 100%;
  left: 0px;
  position: absolute;
  top: 52px;
  color: #000;
  z-index: 1000;
  margin: 0px;
  text-align: left;
  padding: 14px;
  font-size: 15px;
  border: solid 1px #eeeeee;
  background-color: #fff;
  opacity: 0;
  -o-transform-origin: 0% 0%;
  -ms-transform-origin: 0% 0%;
  -moz-transform-origin: 0% 0%;
  -webkit-transform-origin: 0% 0%;
  -o-transition: -o-transform 0.3s, opacity 0.3s;
  -ms-transition: -ms-transform 0.3s, opacity 0.3s;
  -moz-transition: -moz-transform 0.3s, opacity 0.3s;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
}

.wsmenu-list li > .wsmenu-submenu {
  transform-style: preserve-3d;
  -o-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transform: rotateX(-75deg);
  -o-transform: rotateX(-75deg);
  -moz-transform: rotateX(-75deg);
  -webkit-transform: rotateX(-75deg);
  visibility: hidden;
}

.wsmenu-submenu {
  position: absolute;
  top: 52px;
  z-index: 1000;
  margin: 0px;
  padding: 2px;
  border: solid 1px #eeeeee;
  background-color: #fff;
  opacity: 0;
  -o-transform-origin: 0% 0%;
  -ms-transform-origin: 0% 0%;
  -moz-transform-origin: 0% 0%;
  -webkit-transform-origin: 0% 0%;
  -o-transition: -o-transform 0.3s, opacity 0.3s;
  -ms-transition: -ms-transform 0.3s, opacity 0.3s;
  -moz-transition: -moz-transform 0.3s, opacity 0.3s;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
}
.wsmenu-submenu li {
  position: relative;
  padding: 0px;
  margin: 0px;
  height: 30px;
  display: block;
  border-bottom: 1px solid #666666 !important;
}
.wsmenu-submenu li a {
  background: #fff !important;
  background-image: none !important;
  color: #666666;
  border-right: 0 none !important;
  text-align: left;
  display: block;
  line-height: 18px;
  padding: 6px 12px;
  text-transform: none;
  font-size: 15px;
  letter-spacing: normal;
  border-right: 0px solid;
  font-weight: 500 !important;
}

.wsmenu-list li:last-child a {
  -webkit-border-radius: 0px 4px 0px 0px;
  -moz-border-radius: 0px 4px 0px 0px;
  border-radius: 0px 4px 0px 0px;
  border-right: 0px solid;
}
.blue-grdt > .wsmenu-list > li > a {
  background-color: transparent !important;
}
.blue-grdt > .wsmenu-list > li > a {
  color: #fff !important;
}
.wsmenu-list > li > a {
  display: block;
  background-color: #fff;
  color: #424242;
  padding: 12px 20px;
  line-height: 1.5;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  text-decoration: none;
  position: relative;
}

a:focus {
  outline: none !important;
}
a:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

@media only screen and (min-width: 781px) {
  .wsmenu-list li:hover > .wsmenu-submenu {
    display: block !important;
  }
}
.wsmenu-list li:hover .wsmenu-submenu {
  display: block;
}
.wsmenu-list li:hover > .wsmenu-submenu {
  transform: rotateX(0deg);
  -o-transform: rotateX(0deg);
  -moz-transform: rotateX(0deg);
  -webkit-transform: rotateX(0deg);
  opacity: 1;
  visibility: visible;
}
.wsmenu-list li > .wsmenu-submenu {
  transform-style: preserve-3d;
  -o-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transform: rotateX(-75deg);
  -o-transform: rotateX(-75deg);
  -moz-transform: rotateX(-75deg);
  -webkit-transform: rotateX(-75deg);
  visibility: hidden;
}

.wsmenu-click {
  display: none;
}

.wsmenu-list li:hover > a {
  text-decoration: none;
  background-color: rgb(0, 130, 200) !important;
}

@media only screen and (min-width: 230px) and (max-width: 780px) {
  .header {
    box-shadow: none !important;
    background-image: none !important;
    min-height: 54px;
  }
  .wrapper {
    width: 100%;
    margin: 0% 0%;
  }
  .logo {
    display: none !important;
  }
  .searchdestop {
    display: none !important;
  }
  .wsmenu-submenu li {
    height: auto !important;
  }
  .wsmenu .wsmenu-list > li {
    width: 100%;

    display: block;
    float: none;
    border-right: none;
    background-color: transparent;
    position: relative;
    white-space: inherit;
  }

  .wsmenu > .wsmenu-list > li > a {
    padding: 9px 32px 9px 17px;
    font-size: 14px;
    text-align: left;
    border-right: solid 0px;
    background-color: transparent;
    line-height: 25px;
    border-bottom: 1px solid;
    border-bottom-color: rgba(0, 0, 0, 0.13);
    position: static;
  }
  .wsmenu-click i {
    display: block;
    height: 23px;
    width: 25px;
    margin-top: 11px;
    margin-right: 8px;
    background-size: 25px;
    font-size: 21px;
    color: rgba(0, 0, 0, 0.25);
    float: right;
  }
}

@media (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}
.glyphicon {
  position: static;
  top: 0px;
}
.search {
  display: block;
  padding-top: 15px;
  text-align: right;
  margin-right: 50px;
  position: absolute;
  right: 0;
  z-index: 101;
  height: 52px;
}
.glyphicon-search:before {
  content: "\e003";
}

@media only screen and (max-width: 780px) {
  .wsmenu.menuclose {
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  .wsmenu {
    width: 0px;
    height: 100%;
    position: absolute;
    right: 0;
    top: 50px;
    margin: 0;
    background-color: #fff;
    border-radius: 0px;
    z-index: 100;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    border: none !important;
    background-color: #0082c8 !important;
  }
  .overlapblackbg.menuopen {
    display: block;
  }
  .wsmenuexpandermain .menuopen {
    margin-top: 10px !important;
  }
  .wsmenu.menuopen {
    width: 80%;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  .wsmenu {
    width: 0px;
    height: 100%;
    position: absolute;
    right: 0;
    top: 50px;
    margin: 0;
    background-color: #fff;
    border-radius: 0px;
    z-index: 100;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    border: none !important;
    background-color: #0082c8 !important;
  }
  .wsmenu .wsmenu-list {
    height: 100%;
    overflow-y: auto;
    display: block !important;
  }
  .wsmenucontent {
    z-index: 1;
  }
  .overlapblackbg {
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    display: none;
    background-color: rgba(0, 0, 0, 0);
    cursor: pointer;
  }
  .wsmenuexpandermain {
    display: block !important;
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  .animated-arrow {
    cursor: pointer;
    padding: 13px 35px 16px 0px;
    margin: 10px 0 0 15px;
  }
  .animated-arrow {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 200;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  .animated-arrow span,
  .animated-arrow span:before,
  .animated-arrow span:after {
    cursor: pointer;
    height: 3px;
    width: 23px;
    background: #ffffff;
    position: absolute;
    display: block;
    content: "";
  }
  .animated-arrow span:after {
    bottom: -7px;
  }
  .animated-arrow span:before {
    top: -7px;
  }
  .smallogo {
    width: 100%;
    display: block;
    padding-top: 8px;
    text-align: left;
    padding-left: 10px;
    position: absolute;
    z-index: 100;
    width: 100%;
    height: 52px;
    background-color: #0082c8;
  }
  .animated-arrow span {
    cursor: pointer;
    height: 3px;
    width: 23px;
    background: #ffffff;
    position: absolute;
    display: block;
    content: "";
  }
}
.overlapblackbg {
  display: none;
}
.wsmenuexpandermain {
  display: none;
}
.micon {
  float: left;
  color: #ffffff;
  cursor: pointer;
}
